/* Global Styles */
:root {
  width: 1440px;
  height: 900px;
  background: var(--off-white, #FAFAFA);
  font-family: -apple-system, BlinkMacSystemFont, "SF Pro", "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "SF Pro", "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.page-layout {
  display: grid;
  grid-template-columns: 280px 1fr;
  gap: 32px;
  align-items: start;
}

.topfilter-results-layout {
  position: sticky;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.results-layout {
  display: flex;
  width: 1015px;
  align-items: flex-start;
  align-content: flex-start;
  gap: 17px;
  flex-wrap: wrap;
}

/* Header */
.header {
  width: 273.425px;
  height: 40px;
  fill: var(--black, #000);
}



/* Topbar Filter */
.topbar-filter-cards {
  display: inline-flex;
  gap: 10px;
  overflow-x: auto;
}

.topbar-filter-card {
  width: 160px;
  height: 80px;
  border-radius: 8px;
  border: 0.6px solid var(--stroke, rgba(0, 0, 0, 0.10));
  background: var(--white, #FFF);

  /* basic shadow */
  box-shadow: -98px 56px 32px 0 rgba(0, 0, 0, 0.00),
  -63px 36px 29px 0 rgba(0, 0, 0, 0.00),
  -35px 20px 24px 0 rgba(0, 0, 0, 0.00),
  -16px 9px 18px 0 rgba(0, 0, 0, 0.01),
  -4px 2px 10px 0 rgba(0, 0, 0, 0.01);
}

.topbar-filter-label {
  color: var(--black, #000);
  font-feature-settings: 'liga' off, 'clig' off;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%; /* 14px */
  letter-spacing: -0.5px;
}

.topbar-filter-img {
  width: 80px;
  height: 80px;
}

.topbar-filter-card.active {
  background: black;
  color: white;
  border-color: black;
}



/* Sidebar Filter */
.sidebar-filter-parent {
  display: flex;
  width: 239px;
  height: 764px;
  padding: 24px;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  border-radius: 10px;
  border: 0.6px solid var(--stroke, rgba(0, 0, 0, 0.10));
  background: var(--white, #FFF);

  /* basic shadow */
  box-shadow: -98px 56px 32px 0 rgba(0, 0, 0, 0.00),
  -63px 36px 29px 0 rgba(0, 0, 0, 0.00),
  -35px 20px 24px 0 rgba(0, 0, 0, 0.00),
  -16px 9px 18px 0 rgba(0, 0, 0, 0.01),
  -4px 2px 10px 0 rgba(0, 0, 0, 0.01);
}

.sidebar-filter-title {
  color: var(--black, #000);
  font-feature-settings: 'liga' off, 'clig' off;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%; /* 24px */
  letter-spacing: -0.5px;
}

.sidebar-filter-content {
  display: flex;
  flex-direction: column;
}

.filter-subcategory-title {
  color: var(--black, #000);
  font-size: 12px;
  font-style: normal;
  font-weight: 590;
  line-height: 100%; /* 12px */
  letter-spacing: -0.5px;
  text-transform: uppercase;
  opacity: 0.4;
}

.filter-food-category,
.filter-delivery-time,
.filter-price-range {
  margin-bottom: 16px;
}

/* Filter pill layout */
.sidebar-filter-item {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

/* Pills */
.filter-pill {
  padding: 6px 14px;
  border-radius: 999px;
  border: 1px solid rgba(0,0,0,0.1);
  background: white;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.15s ease;
}

.filter-pill:hover {
  background: #f5f5f5;
}

/* Active */
.filter-pill.active {
  background: black;
  color: white;
  border-color: black;
}



/* Filter Result */
.filter-results-parent {
  display: flex;
  width: 1015px;
  align-items: flex-start;
  align-content: flex-start;
  gap: 17px;
  flex-wrap: wrap;
}

.filter-results-title {
  color: var(--black, #000);
  font-feature-settings: 'liga' off, 'clig' off;
  font-size: 40px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%; /* 40px */
  letter-spacing: -0.5px;
}

.results-content {
 display: flex;
  width: 1015px;
  align-items: flex-start;
  align-content: flex-start;
  gap: 17px;
  flex-wrap: wrap;
}



/* RestaurantCard component */
.restaurant-card {
  display: flex;
  width: 327px;
  height: 202px;
  padding: 16px;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  flex-shrink: 0;
  border-radius: 8px;
  border: 0.6px solid var(--stroke, rgba(0, 0, 0, 0.10));
  background: var(--white, #FFF);

  /* basic shadow */
  box-shadow: -98px 56px 32px 0 rgba(0, 0, 0, 0.00),
  -63px 36px 29px 0 rgba(0, 0, 0, 0.00),
  -35px 20px 24px 0 rgba(0, 0, 0, 0.00),
  -16px 9px 18px 0 rgba(0, 0, 0, 0.01),
  -4px 2px 10px 0 rgba(0, 0, 0, 0.01);
}

.lower-text {
  color: var(--black, #000);
  font-feature-settings: 'liga' off, 'clig' off;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%; /* 12px */
  letter-spacing: -0.5px;
}

.restaurant-name {
  width: 213px;
  color: var(--black, #000);
  font-feature-settings: 'liga' off, 'clig' off;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%; /* 24px */
  letter-spacing: -0.5px;
}

.restaurant-open {
  display: flex;
  padding: 8px 12px 8px 10px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  border-radius: 88px;
  border: 0.6px solid var(--stroke, rgba(0, 0, 0, 0.10));
  background: var(--white, #FFF);

  /* basic shadow */
  box-shadow: -98px 56px 32px 0 rgba(0, 0, 0, 0.00),
  -63px 36px 29px 0 rgba(0, 0, 0, 0.00),
  -35px 20px 24px 0 rgba(0, 0, 0, 0.00),
  -16px 9px 18px 0 rgba(0, 0, 0, 0.01),
  -4px 2px 10px 0 rgba(0, 0, 0, 0.01);
}

